import React, { useState, useEffect } from "react";
import { withRouter, useHistory } from "react-router-dom";
import { firebase } from "../../../firebase.js";

/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx, css } from "@emotion/react";
import { DivCSS, BoxDivCSS, Logo, FormDivCSS } from "../css/UserPageElement.js";
import MobileFooter from "../../HeaderAndFooter/Footer/MobileFooter.js";
import axios from "axios";
import shortId from "shortid";

function RegisterPage() {
  //
  const [Email, setEmail] = useState("");
  const [Name, setName] = useState("");
  const [Password, setPassword] = useState("");
  const [ConfirmPassword, setConfirmPassword] = useState("");

  // 
  const [Key, setKey] = useState("");
  const [InputKey, setInputKey] = useState("");
  const [minutes, setMinutes] = useState(0);
  const [seconds, setSeconds] = useState(0);
  const [EmailCheck, setEmailCheck] = useState(true); //  
  const [EmailCheckVerification, setEmailCheckVerification] = useState(false); //   

  // 
  const [ErrorFormSubmit, setErrorFormSubmit] = useState("");
  const [Loading, setLoading] = useState(false);

  let history = useHistory();

  const EmailVerification = () => {
    let temp = shortId.generate();
    let body = {
      email: Email,
      key: temp,
      name: Name,
    };
    setKey(temp);
    axios.post("api/user/sendEmail", body).then((response) => {
      if (response.data.success) alert(" .");
    });
  };

  const verifyEmail = (email) => {
    var regExp =
      /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
    if (email.match(regExp) != null) return true;
    else return false;
  };

  const StartTimer = async () => {
    if (!(Email && Name)) {
      return alert("   .");
    }
    if (!verifyEmail(Email)) {
      return alert("  .");
    }
    var EmailCheckFunc = new Promise((resolve, reject) => {
      let body = {
        email: Email,
      };
      try {
        axios.post("api/user/checkEmail", body).then((response) => {
          if (!response.data.success) reject(response.data.success);
          if (!response.data.doc) {
            //  
            setEmailCheck(false);
            resolve(false);
          } else {
            //  
            setEmailCheck(true);
            resolve(true);
          }
        });
      } catch (error) {
        reject(error);
      }
    });

    EmailCheckFunc.then((val) => {
      if (val) {
        return alert("  .");
      } else {
        EmailVerification();
        setMinutes(parseInt(3));
        setTimeout(() => {
          setKey("");
        }, 180000);
      }
    }).catch((reason) => {
      console.log(reason);
    });
  };

  useEffect(() => {
    const countdown = setInterval(() => {
      if (parseInt(seconds) > 0) {
        setSeconds(parseInt(seconds) - 1);
      }
      if (parseInt(seconds) === 0) {
        if (parseInt(minutes) === 0) {
          clearInterval(countdown);
        } else {
          setMinutes(parseInt(minutes) - 1);
          setSeconds(59);
        }
      }
    }, 1000);
    return () => clearInterval(countdown);
  }, [minutes, seconds]);

  const onSubmitHandler = async (e) => {
    e.preventDefault();
    if (Password !== ConfirmPassword) {
      return alert("    .");
    }
    if (!EmailCheckVerification) {
      return alert("   .");
    }

    let body = {
      email: Email,
      password: Password,
      name: Name,
    };

    try {
      setLoading(true);
      let createdUser = await firebase
        .auth()
        .createUserWithEmailAndPassword(body.email, body.password);

      await createdUser.user.updateProfile({
        displayName: body.name,
        photoURL: `https://kr.object.ncloudstorage.com/makeit/user/profile.png`,
      });

      let user = {
        uid: createdUser.user.uid,
        displayName: createdUser.user.displayName,
        email: body.email,
        photoURL: createdUser.user.photoURL,
      };

      await axios.post("/api/user/register", user).then((response) => {
        if (response.data.success) {
          console.log("success");
        } else {
          console.log("false");
        }
      });

      //Firebase  
      await firebase.database().ref("users").child(createdUser.user.uid).set({
        name: createdUser.user.displayName,
        image: createdUser.user.photoURL,
      });

      setLoading(false);
      //    push
      history.push("/");
    } catch (error) {
      console.log(error);
      setErrorFormSubmit(error.message);

      setTimeout(() => {
        setErrorFormSubmit("");
        setLoading(false);
      }, 5000);
    }
  };

  return (
    <>
      <div css={DivCSS}>
        <div css={BoxDivCSS}>
          <div css={Logo}>
            <img src={process.env.PUBLIC_URL + "/Img/logo.png"} alt="" />
            <p>
               ,
              <br />
                
            </p>
          </div>
          <form css={FormDivCSS} onSubmit={onSubmitHandler}>
            <label></label>
            <input
              type="text"
              value={Name}
              onChange={(e) => setName(e.currentTarget.value)}
              required
            />

            <label></label>
            <input
              type="email"
              value={Email}
              onChange={(e) => setEmail(e.currentTarget.value)}
              required
              disabled={EmailCheckVerification ? true : false}
            />
            {!EmailCheckVerification && (
              <button
                type="button"
                onClick={() => StartTimer()}
                disabled={Key ? true : false}
              >
                 
              </button>
            )}

            {Key && !EmailCheckVerification ? (
              <>
                <div style={{ textAlign: "left", width: "100%" }}>
                  <p>
                    
                    <span style={{ color: "red", marginLeft: "1rem" }}>
                      {minutes}:{seconds < 10 ? `0${seconds}` : seconds}
                    </span>
                  </p>
                  <br />
                  <input
                    type="verification"
                    value={InputKey}
                    onChange={(e) => setInputKey(e.currentTarget.value)}
                  />
                  <button
                    type="button"
                    onClick={() => {
                      if (Key === InputKey) {
                        setEmailCheckVerification(true);
                        alert("  .");
                      }
                    }}
                  >
                     
                  </button>
                </div>
              </>
            ) : null}

            <label></label>
            <input
              type="password"
              value={Password}
              onChange={(e) => setPassword(e.currentTarget.value)}
              required
            />

            <label></label>
            <input
              type="password"
              value={ConfirmPassword}
              onChange={(e) => setConfirmPassword(e.currentTarget.value)}
              required
            />

            <br />
            {ErrorFormSubmit && <p>{ErrorFormSubmit}</p>}
            <button type="submit" disabled={Loading}>
               
            </button>
          </form>
        </div>
      </div>
      <MobileFooter />
    </>
  );
}

export default withRouter(RegisterPage);
