import React from "react";
import { PCOnly } from "../../css/MyPageElement.js";
import Avatar from "react-avatar";
import { useSelector } from "react-redux";

function BasicMyPage(props) {
  const user = useSelector((state) => state.user);
  return (
    <div>
      <div className="profile">
        <h1></h1>
        <p>
          <PCOnly>
                   !
          </PCOnly>
        </p>
        <div className="profileContainer">
          <Avatar
            src={user.userData.photoURL}
            size="50"
            round={true}
            style={{ border: "1px solid #c6c6c6" }}
            className="img"
          />
          <p className="name">{user.userData.displayName}</p>
          <p className="email">{user.userData.email}</p>
          <div
            className="next"
            onClick={() => {
              props.setTaps("");
            }}
          >
            <span></span>
          </div>
        </div>
        <h1></h1>
        <p>
          <PCOnly>
              , ,     !
          </PCOnly>
        </p>
        <div></div>
      </div>

      <div className="btnDiv">
        <div className="topLeft"  onClick={() => { props.setTaps("");}}></div>
        <div className="topRight" onClick={() => { props.setTaps("");}}>/</div>
        <div className="bottomLeft">/</div>
        <div className="bottomRight"></div>
      </div>
    </div>
  );
}

export default BasicMyPage;
